

.greenbin {
  align-self: flex-end;
  line-height: 1; /* Ensures the line height is exactly the text height */
  margin-bottom: 20px; /* Adds margin between the header and the image */
}

.image-wrapper {
  width: 70%; /* Sets the image width to 70% of its parent */
  margin-bottom: 20px; /* Adds margin between the image and the text */
}

.startimgIcon {
  width: 100%; /* Ensures the image takes up 100% of the wrapper's width */
  height: auto; /* Maintains the aspect ratio */
}

.manageYourWaste {
  margin-bottom: 20px; /* Adds margin between the text and the button */
}

.getStartedBtn1 {
  align-self: center; /* Centers the button horizontally */
}
